

.fabBtn{
   box-shadow: none !important;
   background-color: #141414;
   color: blanchedalmond;
   border-radius: 25%;
   top: 25px;
   font-size: 30px;
   padding: 15px;
   position: fixed;
   right: 25px;
   z-index: 1;
   line-height: 1;
}

.csCont {
 padding-top: 35%;
 padding-left: 25%;
}


.iconSize {
 width: 30px;
 height: 30px;
}

.contactBtn {
 color: #08fdd8;
 background: transparent;
 border-color: #08fdd8;
 border-width: 3px;
 font-size: 14px;
 letter-spacing: 4px;
 font-family: "Open Sans";
 text-decoration: none;
 margin-top: 20px;
 position: relative;
 width: 100%;
 max-width: 220px;
 height: 50px;
 line-height: 50px;
 display: block;
 text-align: center;

 -webkit-transition: 0.3s ease-out all;
 -o-transition: 0.3s ease-out all;
 transition: 0.3s ease-out all;

}
.contactBtn:hover {
 background-color: #08fdd8;
 color: #141414;
 z-index: 5;
}

.contactScreen {
 padding-top: 13%;
}

.scrTitle{
 color: #08fdd8;
 font-size: 80px;
 font-family: "Open Sans",sans-serif;
 font-weight: 800;
 font: bold;
}


.descHm {
   font-family: 'Open Sans';
   font-size: 1.7rem;
   color:#737373;
   margin-bottom: 8%;
 }

 .inputContact {
    background-color: #2b2b2b;
    color: #717171;
 }

 .inputContactContainer {
   display: grid;
   grid-template-columns: auto auto;
 }

 .half1 {
   grid-column: 1;
   padding-inline: 10px;
   position: relative;
   width: auto;
  }
.half2 {
  grid-column: 2;
  padding-inline: 10px;
  position: relative;
  width: auto;
}
.rowIn {
  grid-column: 1/3;
  padding-inline: 10px;
  position: relative;
}

.newForm{
  display: block;
  width: 100%;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;

  
}

.newForm:focus {
  border-color: #05fdd8;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}
.contactScreen p {
  font-size: 1.25rem;
}

.formbtn{
  grid-column: 2/3;
  padding-inline: 10px;
  display: flex;
  flex-direction: row-reverse;
}


.sidebar {
   text-align: center;
   height: 100%;
   width: 0;
   position: fixed;
   z-index: 1;
   top: 0;
   right: 0;
   background-color: #1d1d1d;
   overflow-x: hidden;
   padding-top: 60px;
 }
 .expandSidebar {
   width: 100vw;
 }
 .sidebar a {
   padding: 8px 8px 8px 32px;
   text-decoration: none;
   font-size: 25px;
   color: #818181;
   display: block;
   transition: 0.3s;
 }
 
 
@media screen and (max-height: 450px) {
   .sidebar {padding-top: 15px;}
   .sidebar a {font-size: 18px;}
 }

